,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,eAAe;QACf,cAAc;QACd,gBAAgB;QAChB,iBAAiB;QACjB,eAAe;AACnB;AACA;QACI,eAAe;QACf,iBAAiB;QACjB,kBAAkB;QAClB,cAAc;QACd,eAAe;AACnB;AACA;QACI,mBAAmB;QACnB,4BAA4B;QAC5B,cAAc;QACd,yBAAyB;QACzB,0CAA0C;QAC1C,kBAAkB;QAClB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,0BAA0B;QAC1B,gCAAgC;QAChC,mBAAmB;QACnB,kBAAkB;QAClB,mBAAmB;QACnB,iBAAiB;QACjB,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,eAAe;QACf,cAAc;AAClB;AACA;QACI,mBAAmB;QACnB,eAAe;AACnB;AACA;QACI,kBAAkB;QAClB,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;AAClB;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,eAAe;QACf,iBAAiB;QACjB,kBAAkB;QAClB,gBAAgB;QAChB,cAAc;QACd,iBAAiB;QACjB,YAAY;QACZ,aAAa,EAAE,+CAA+C;AAClE;AACA;QACI,eAAe;QACf,iBAAiB;QACjB,kBAAkB;QAClB,cAAc;AAClB;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,YAAY;QACZ,iBAAiB;QACjB,eAAe;QACf,gBAAgB;QAChB,cAAc;QACd,aAAa;QACb,mBAAmB;QACnB,mBAAmB;QACnB,YAAY;QACZ,kBAAkB;QAClB,YAAY;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { IUser_SUSI } from \"@stechquick/algae/lib/qCloudTemp/authentication\";\nimport { IListInvitationsResponseItem } from \"@stechquick/algae/lib/qCloudTemp/symDtoObjects\";\nimport { computed, defineAsyncComponent, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IOrganizationMember, IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n\n\n\nconst router = container.resolve<IRouter>(IRouter);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst selectedOrganizationName = computed(() => viewModel.user!.selectedOrganization?.name);\nconst selectedOrganizationCreaterId = computed(() => viewModel.user!.selectedOrganization?.creatorUserId);\n\n\n// const routerParams = router.getRouteParams<{ orgId: string }>();\n\n// const members = ref<Array<IOrganizationMember>>([]);\nconst joinedMembers = ref<Array<IUser_SUSI>>([]);\nconst waitingMembers = ref<Array<IListInvitationsResponseItem>>([]);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nconst goBack = () => {\n    router.push(`/main/applicationList/${viewModel.user!.selectedOrganization!.id}`);\n}\nfunction calculateDate(createDate: string, format: boolean) {\n    if(!format) {\n        const date = new Date(createDate);\n        if(date) {\n            const minutes = parseInt(String((date.getTime() - (new Date()).getTime()) / (60 * 1000)), 10)\n            const hours = parseInt(String(minutes / 60), 10);\n            const days = parseInt(String(hours / 24), 10);\n            const hoursCalc = hours - (days*24); \n            if(hours > 0) {\n                return days.toFixed() + ` day${days < 2 ? '' : 's'} ` + hoursCalc.toFixed() + ` hour${hoursCalc < 2 ? '' : 's'} `;\n            } \n\n            else {\n                return \"< 1 hour\";\n            }\n        }\n    }\n    else {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(createDate));\n        return formattedDate;\n    }\n    \n}\nconst cancelInvitation = async (invitationId: string) => {\n    executor.execute(async () => {\n        await organization.deleteInvitation({ invitationId });\n        await refreshData();\n    }, { loading: true });\n};\nconst removeFromOrganization = async (userId: string) => {\n    executor.execute(async () => {\n        await organization.removeUserFromOrganization({ userId });\n        await refreshData();\n    }, { loading: true });\n};\nfunction openInvitePeopleDialog () {\n    const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n        if(reason == \"component\") {\n            refreshData();\n        }\n    }\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/organization/invitePeopleDialog.vue`)),\n        { closable: true, title: \"Invite People to \" + selectedOrganizationName.value, width: \"658px\", maxHeight: \"700px\", closeCb},\n        \n    );\n}\nconst refreshData = async () => {\n    // const response = await organization.getOrganizationMembers({});\n    // members.value = [];\n    // response.forEach(member => members.value.push(member));\n    executor.execute(async () => { \n        await refreshOrganizationInvitationsData();\n        await refreshOrganizationUsersData();\n    }, { loading: true });\n};\nconst refreshOrganizationUsersData = async () => {\n    const response = await organization.listOrganizationUsers();\n    joinedMembers.value = [];\n    response.forEach(member => joinedMembers.value.push(member));\n};\nconst refreshOrganizationInvitationsData = async () => {\n    const response = await organization.listOrganizationInvitations();\n    waitingMembers.value = [];\n    response.forEach(member => waitingMembers.value.push(member));\n};\nexecutor.execute(async () => { await refreshData() }, { loading: true });\n\n\n\n</script>\n<template>\n    <div class=\"organization-header\">\n        <div class=\"back-button-div\" @click=\"goBack\">\n            <i class=\"mdi mdi-arrow-left-box back-button\"></i>\n        </div>\n        <hr style=\"width: 1px; height: 36px; color: #D5D5D5; margin: 0; opacity: 1\">\n        <div class=\"organization-name\">\n            <span>{{ selectedOrganizationName }}</span>\n            <i class=\"mdi mdi-pencil\" style=\"font-size: 20px; color: #757575; margin-left: 14px; display: none;\"></i>\n        </div>\n        <div style=\"margin: 0 0 0 auto;\">\n            <button class=\"delete-organization-button\"><i class=\"mdi mdi-delete\" style=\"font-size: 16px; margin-right: 4px;\"></i>DELETE ORGANIZATION</button>\n        </div>\n    </div>\n    <div style=\"margin-top: 24px\">\n        <button class=\"invite-button\" @click=\"openInvitePeopleDialog\"><i class=\"mdi mdi-account-multiple-plus\" style=\"font-size:20px; margin-right: 4px;\"></i>INVITE PEOPLE</button>\n    </div>\n    <p class=\"app-text\">Invitations ({{waitingMembers.length}})</p>\n    <table v-if=\"waitingMembers.length > 0\" class=\"members-table\">\n            <tr class=\"thead\">\n                <th class=\"col-xl-5\">Mail Address</th>\n                <th class=\"col-xl-3\">Date Invited</th>\n                <th class=\"col-xl-3\">Invitation Expires in</th>\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"member in waitingMembers\">\n                <td><img src=\"../../../../images/profile-image.svg\" style=\"margin-right: 12px;\"/>{{ member.email }}</td>\n                <td>{{ calculateDate(member.createDate, true) }}</td>\n                <td>{{ calculateDate(member.expireDate, false) }}</td>\n                <td>\n                    <i class=\"mdi mdi-dots-vertical member-edit-menu-button\" id=\"dropdownMemberEditMenuButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"></i>\n                    <div style=\"right: 0\" class=\"dropdown-menu member-edit-menu\" aria-labelledby=\"dropdownMemberEditMenuButton\">\n                        <span style=\"display:flex; align-items: center; cursor: pointer;\" @click=\"cancelInvitation(member.id)\"><i class=\"mdi mdi-close remove-icon\"></i>Cancel Invitation</span>\n                    </div>\n                </td>\n            </tr>\n    </table>\n    <div v-else style=\"display: flex; justify-content: center;\">\n        <span class=\"no-invitation-waiting\">There is no invitation waiting.</span>\n    </div>\n    <p class=\"app-text\">Members ({{joinedMembers.length}})</p>\n    <table class=\"members-table\">\n            <tr class=\"thead\">\n                <th class=\"col-xl-5\">Mail Address</th>\n                <th class=\"col-xl-3\">Name</th>\n                <!-- <th class=\"col-xl-3\">Date Joined</th> -->\n                <!-- <th>Status <span><i class=\"mdi mdi-refresh refresh-table\" @click=\"refreshData\"></i></span></th> -->\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"member in joinedMembers\">\n                <!-- <td v-if=\"member.status == 'joined'\">{{ member.name }}</td>\n                <td v-else></td> -->\n                <td><img src=\"../../../../images/profile-image.svg\" style=\"margin-right: 12px;\"/>{{ member.email }}</td>\n                <td><span>{{ member.name + ' ' + member.lastName + ' ' }}</span><span v-if=\"member.id == selectedOrganizationCreaterId\">(Owner)</span></td>\n                <!-- <td>Date Joined</td> -->\n                <td>\n                    <i class=\"mdi mdi-dots-vertical member-edit-menu-button\" id=\"dropdownMemberEditMenuButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"></i>\n                    <div style=\"right: 0\" class=\"dropdown-menu member-edit-menu\" aria-labelledby=\"dropdownMemberEditMenuButton\">\n                        <span style=\"display:flex; align-items: center; cursor: pointer;\" @click=\"removeFromOrganization(member.id)\"><i class=\"mdi  mdi-account-remove-outline remove-icon\"></i>Remove From Organization</span>\n                    </div>\n                </td>\n            </tr>\n    </table>\n</template>\n<style scoped>\n    * {\n        font-family: 'Roboto';\n    }\n    .app-text {\n        font-size: 22px;\n        color: #151233;\n        font-weight: 500;\n        margin-left: 7px;\n        margin-top: 32px;\n    }\n    .members-table {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        width: 100%;\n    }\n    td:hover {\n        background-color: none !important;\n    }\n    tbody > tr:hover {\n        background-color: none !important;\n        border: none !important;\n    }\n    .members-table th\n     {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 17px 86px 17px 36px;\n    }\n    .members-table td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 13px 86px 13px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .members-table .thead {\n        background: #F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .refresh-table {\n        font-size: 16px;\n        color: #757575;\n        margin-left: 8px;\n        line-height: 16px;\n        cursor: pointer;\n    }\n    .member-edit-menu-button {\n        font-size: 24px;\n        line-height: 24px;\n        text-align: center;\n        color: #757575;\n        cursor: pointer;\n    }\n    .member-edit-menu {\n        background: #FFFFFF; \n        padding: 18px 32px 18px 12px;\n        color: #212121;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        border-radius: 8px;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .remove-icon {\n        font-size: 20px;\n        margin-right: 8px;\n    }\n    .organization-header {\n        padding: 5px 24px 5px 28px;\n        border-bottom: 1px solid #D5D5D5;\n        background: #EEEEEE; \n        margin-left: -33px;\n        margin-right: -33px;\n        margin-top: -22px;\n        display: flex;\n        align-items: center;\n    }\n    .back-button {\n        font-size: 32px;\n        color: #151233;\n    }\n    .back-button-div {\n        padding-right: 20px;\n        cursor: pointer;\n    }\n    .organization-name {\n        padding-left: 16px;\n        font-weight: 500;\n        font-size: 20px;\n        line-height: 23px;\n        color: #212121;\n    }\n    .delete-organization-button {\n        background: #CBCBCB;\n        border-radius: 4px;\n        font-size: 14px;\n        line-height: 16px;\n        text-align: center;\n        font-weight: 500;\n        color: #212121;\n        padding: 8px 16px;\n        border: none;\n        display: none; /**Organization silme özelliği gelince kaldır */\n    }\n    .no-invitation-waiting {\n        font-size: 18px;\n        line-height: 26px;\n        text-align: center;\n        color: #BCBCBC;\n    }\n    .invite-button {\n        background: #2C5A86; \n        border-radius: 4px; \n        border: none; \n        padding: 8px 16px;\n        font-size: 15px; \n        font-weight: 500; \n        color: #FFFFFF;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        height: 36px;\n        margin-right: 12px;\n        float: right;\n    }\n</style>"],"sourceRoot":""}]);
