import { IUser_SUSI } from "@stechquick/algae/lib/qCloudTemp/authentication";
import { IOrganization } from "@stechquick/algae/lib/qCloudTemp/membership";
import { IDeleteInvitationRequest, IInvitationRequest, IListInvitationsResponseItem, IListOrganizationsResponse, IPageable, IRemoveUserFromOrganizationRequest, IUpdateInvitationRequest } from "@stechquick/algae/lib/qCloudTemp/symDtoObjects";
import { IApplication } from "../model/models";

export type IOrganizationMember = { name?: string, email: string, status: "joined", userId: string } | { status: "waiting", email: string, invitationId: string, invitationDate: string, expirationDate: string };
export interface IOrganizationUseCase {
    listOrganizations(): Promise<Array<IListOrganizationsResponse>>;
    createOrganization(name: IOrganization["name"]): Promise<IOrganization>;
    changeOrganization(organizationId: string): Promise<Array<IApplication>>;
    listOrganizationInvitations(): Promise<Array<IListInvitationsResponseItem>>;
    listOrganizationUsers(): Promise<Array<IUser_SUSI>>;

    // getOrganizationMembers(options: { pagination?: IPageable }): Promise<Array<IOrganizationMember>>;
    createInvitation(request: IInvitationRequest): Promise<void>;
    deleteInvitation(request: IDeleteInvitationRequest): Promise<void>;
    removeUserFromOrganization(request: IRemoveUserFromOrganizationRequest): Promise<void>;
    respondToInvitation(response: IUpdateInvitationRequest): Promise<void>;
}
export const IOrganizationUseCase = Symbol.for("IOrganizationUseCase");